import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, compareJSON, fetchEncryptStream, qryStore } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";
import { SelectDropAccount_Cmpt } from "../../ComprobanteParametrizados";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const CheckBox = CheckBox_Cmpt();

const SelectDropAccount = SelectDropAccount_Cmpt();


const frmNm = "upd_provider"

const UpdAccount  = ({data, confirm}) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	let flds = ["type","name", "description", "account", "subAccount"]

	const save  = async ()  => {
		let ffm = getStore(frmNm);
		let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )

	
		let bdyq2 = {...getStore(qryStore())?.upd_prov_cust};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId,
			"providerId": ffm["providerId"],
		}
		bdyq2.data2update=diff.data;

		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			updStore(frmNm + "_bck", {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
		
	};

	
	let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )




	const clean = (f) => {
		updStore(frmNm, {});
		set_obs(gen16CodeId())
	};

	const handleAddAccount = (acc,subacc) => {
		let frm2 = getStore(frmNm) || {};
		frm2["account"] = acc;
		frm2["subAccount"] = subacc;
		updStore(frmNm, frm2);
		set_obs(gen16CodeId())
	}

	const changeAccount = (acc,subacc) => {
		let frm2 = getStore(frmNm) || {};
		frm2["account"] = null;
		frm2["subAccount"] = null;
		updStore(frmNm, frm2);
		set_obs(gen16CodeId())
	}

	

	return (
		<div
		style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 25px"
		}}
		>
			<div className="_dsplFlx ">
				<p className="form_title">
					Actualizar Provedor o Cliente
				</p>
				<div className="flexSpace"/>
				
			</div>

			<TextInput  
				init={getStore(frmNm)?.["name"]} 
				label={"Nombre"} 
				width={520} 
				updChanges={(e)=>updFrm("name",e)}
			/>
		
			
			<div className={"extra_container"}>
					{getStore(frmNm)?.["account"]?
						<div className=""  >
							<div className="_dsplFlx"  >
								<div className="employee_title" style={{color: "#646cff", cursor: "pointer"}} onClick={()=>changeAccount()}> 
									Cuenta:
								</div>
								<div className="employee_name">
									{getStore("account_ids")?.[getStore(frmNm)["account"]]?.name}
								</div>
							</div>
							{getStore(frmNm)?.["subAccount"]?
								<div className="_dsplFlx"  >
									<div className="employee_title" style={{color: "#646cff", cursor: "pointer"}}> 
										SubCuenta:
									</div>
									<div className="employee_name">
										{getStore("sub_account_ids")?.[getStore(frmNm)["subAccount"]]?.subAccountName}
									</div>
								</div>
							:null}
						</div>
					:
					<SelectDropAccount
						placeholder={"buscar una cuenta"} 
						width={420}
						handleClick={handleAddAccount}
					/>
					}
				</div>

				<TextInput  
					init={getStore(frmNm)?.["description"]} 
					label={"Descripcion"} 
					width={520} 
					updChanges={(e)=>updFrm("description",e)}
				/>
				
		

				<div className="_dsplFlx " style={{padding: "18px"}}>
					<div className="flexSpace"/>
					<CheckBox data={[
							{label: "Provedor", id:"provider"},
							{label: "Cliente", id:"customer"}
						]}
						active={getStore(frmNm)?.["type"]}
						updSelect={(e)=>updFrm("type",e)}
					/>
					<div className="flexSpace"/>
				</div>

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{diff?.hasChange ?
				<div className="card">
					<button onClick={save}>
						Actualizar
					</button>
				</div>
				:null}
			</div>
		

			
		</div>
	);
};

export default UpdAccount;
